.Wait .title{
    background:#b3b3b3;
    color:white;
    height:50px;
    line-height:50px;
    padding:0 20px;
    overflow:hidden;
}
.Wait .title img{
    width:23px;
    vertical-align: middle;
}
.Wait .title .list,.title-font{
    float:left;
}
.Wait .title .list{
    padding-right:10px;
}
.Wait .title .more{
    text-align:right;
}


.Wait .content{
    border-bottom:1px solid #f1f0f0;
}
.Wait .content .border img{
    width:30px;
    margin-right:10px;
    line-height:20px;
    margin-top:3px;
}

.Wait .content .click{
    color:#676767;
    flex:1;
}
.Wait .content .click .click-message{
    font-size:12px;
    margin-top:5px;
    color:#4c9e9e;
}
.Wait .content .click .click-message .repeat{
    margin-left:8px;
}
.Wait .content .move{
    color:#b3b3b3;
    font-size:14px;
    padding-right:0;
}
.Wait .content .list-finish{
    color:#4c9e9e;
    font-size:14px;
    padding-right:10px;
}

.Wait .ok-finish,.no-finish{
    background:#e2e2e2;
    height:30px;
    line-height:30px;
    padding-left:20px;
    color:white;
}
.Wait .ok-finish{
    margin-top:5px;
}
.Wait .no-success{
    background:#fbfbfb;
}
.Wait .no-success .content-name{
    color:#ccc;
}

.Wait .move img{
    width:22px;
    vertical-align: middle;
}