*{
    margin:0;
    padding:0;
}
body{
    background:#676767;
}
.Login{
    padding:20px 30px;
}
.Login .title{
    color:white;
    font-size:27px;
}
.Login .title img{
    width:25px;
    vertical-align: middle;
    padding:1px 10px 0 0;
    margin-top:-5px;
}
.Login .register{
    padding:10px 0;
    font-size:11px;
    color:#b3b3b3;
}
.Login .register a{
    color:#d6d6d6;
}

input::-webkit-input-placeholder{
    color:#d6d6d6;
}
input::-moz-placeholder{   /* Mozilla Firefox 19+ */
    color:#d6d6d6;
}
input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
    color:#d6d6d6;
}
input:-ms-input-placeholder{  /* Internet Explorer 10-11 */ 
    color:#d6d6d6;
}
.Login .account,.password{
    margin-top:10px;
    width:100%;
    height:38px;
    line-height:38px;
    border-radius:30px;
    background:#7b7b7b;
}
.Login .account input{
    border:none;
    background:none;
    outline:none;
    color:white;
    caret-color:white;
}
.Login .account img{
    width:19px;
    vertical-align: middle;
    padding-left:13px;
    padding-right:6px;
}
.Login .password{
    margin-top:20px;
}
.Login .password img{
    width:19px;
    vertical-align: middle;
    padding-left:13px;
    padding-right:6px;
}
.Login .password input{
    border:none;
    background:none;
    width:65%;
    outline:none;
    color:white;
    caret-color:white;
}
.Login .password .code{
    font-size:12px;
    color:#d6d6d6;
}

.Login .point{
    color:#b3b3b3;
    font-size:10px;
    margin-top:13px;
}
.Login .regist{
    display:inline-block;
    margin-top:20px;
    background:white;
    font-size:12px;
    padding:6px 5px;
    border-radius:30px;
}
.Login .regist .regist-font{
    padding:0 7px;
    color:#676767;
}
.Login .regist img{
    width:15px;
    vertical-align: middle;
}


.Login .errormessage{
    font-size:14px;
    color:#fd7e7e;
}