*{
    margin:0;
    padding:0;
}
body{
    background:#676767;
}
.Register{
    padding:20px 30px;
}
.Register .title{
    color:white;
    font-size:27px;
}
.Register .title img{
    width:25px;
    vertical-align: middle;
    padding:1px 10px 0 0;
    margin-top:-5px;
}
.Register .login{
    padding:10px 0;
    font-size:11px;
    color:#b3b3b3;
}
.Register .login a{
    color:#d6d6d6;
}

input::-webkit-input-placeholder{
    color:#d6d6d6;
}
input::-moz-placeholder{   /* Mozilla Firefox 19+ */
    color:#d6d6d6;
}
input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
    color:#d6d6d6;
}
input:-ms-input-placeholder{  /* Internet Explorer 10-11 */ 
    color:#d6d6d6;
}
.Register .nickname input{
    border:none;
    background:none;
    outline:none;
    caret-color: white;
    color:white;
}
.Register .nickname img{
    width:21px;
    vertical-align: middle;
    padding-left:13px;
    padding-right:6px;
}

.Register .account,.password,.nickname{
    margin-top:10px;
    width:100%;
    height:38px;
    line-height:38px;
    border-radius:30px;
    background:#7b7b7b;
}
.Register .account{
    margin-top:20px;
}
.Register .account input{
    border:none;
    background:none;
    outline:none;
    caret-color: white;
    color:white;
}
.Register .account img{
    width:19px;
    vertical-align: middle;
    padding-left:13px;
    padding-right:6px;
}
.Register .password{
    margin-top:20px;
}
.Register .password img{
    width:19px;
    vertical-align: middle;
    padding-left:13px;
    padding-right:6px;
}
.Register .password input{
    border:none;
    background:none;
    width:65%;
    outline:none;
    caret-color: white;
    color:white;
}
.Register .password .code{
    color:#c5c5c5;
    font-size:13px;
    background:none;
    border:none;
    width:65px;
    text-align:center;
}
.Register .error{
    color:#fd7e7e;
    font-size:14px;
}

.Register .point{
    color:#b3b3b3;
    font-size:10px;
    margin-top:13px;
}
.Register .regist{
    display:inline-block;
    margin-top:20px;
    background:white;
    font-size:12px;
    padding:6px 5px;
    border-radius:30px;
}
.Register .regist .regist-font{
    padding:0 7px;
    color:#676767;
}
.Register .regist img{
    width:17px;
    vertical-align: middle;
}
.Register .captchaerror,.registererror{
    color:#fd7e7e;
    font-size:14px;
}

.Register .nicknameError,.mobileError,.passwordError{
    color:#fd7e7e;
    font-size:14px;
}



/* 图片验证 */
.Register #hidden{
    display:none;
}
.Register #block{
    display:block;
}
.Register .captcha{
    position:absolute;
    background:rgba(0 ,0,0,0.8);
    height:100%;
    width:100%;
    top:0;
    right:0; 
}
.Register .captcha .captcha-bor{
    background:white;
    border-radius:10px;
    width:300px;
    height:280px;
    margin:90px auto 0;
    text-align:center;
}
.Register .captcha .safe{
    padding-top:20px;
    color:#676767;
}
.Register .captcha .captcha-capt{
    overflow:hidden;
    margin:20px 30px 0;
    height:62px;
    line-height:60px;
}
.Register .captcha .captcha-capt .picture{
    border:1px solid #676767;
    width:130px;
    height:60px;
    float:left;
}
.Register .captcha .captcha-capt button{
    border:none;
    background:none;
    color:#676767;
    outline:none;
}
.Register .captcha  input{
    margin:20px 0 0;
    width:80%;
    height:30px;
    border:none;
    border-bottom:1px solid #ccc;
    outline:none;
    font-size:16px;
}
.Register .captcha .ok button{
    margin-top:15px;
    width:80%;
    height:30px;
    border:none;
    background:#676767;
    color:white;
}