.ClickContent .title{
    background:#b3b3b3;
    color:white;
    height:50px;
    line-height:50px;
    padding:0 20px;
}
.ClickContent .title img{
    width:20px;
    vertical-align: middle;
    margin:-3px 15px 0 0;
}
.ClickContent .aname{
    font-size:14px;
    padding:12px 20px;
    border-bottom:1px solid #f1f0f0;
    color:#676767;
}
.ClickContent .aname img{
    width:30px;
    vertical-align: middle;
    padding-right:5px;
    margin-left:-5px;
}
.ClickContent .aname input{
    width:90%;
    height:25px;
    border:none;
    color:#676767;
    outline:none;
}
.ClickContent .time,.finish,.repreat,.ok{
    font-size:14px;
    padding:16px 20px;
    border-bottom:1px solid #f1f0f0;
    color:#676767;
}
.ClickContent .time img{
    width:21px;
    vertical-align: middle;
    padding-right:10px;
    margin-top:-3px;
}
.ClickContent .time input{
    border:none;
    margin-left:3px;
    outline:none;
    color:#676767;
}
.ClickContent .finish img{
    width:21px;
    vertical-align: middle;
    padding-right:10px;
    margin-top:-3px;
}
.ClickContent .repreat img{
    width:21px;
    vertical-align: middle;
    padding-right:10px;
    margin-top:-3px;
}
.ClickContent .ok img{
    width:21px;
    vertical-align: middle;
    padding-right:10px;
    margin-top:-2px;
}
.ClickContent .btn-save{
    margin:20px 0 0 20px;
}
.ClickContent .btn-save button{
    border:none;
    background:#4c9e9e;
    border-radius:3px;
    padding:8px 15px;
    color:white;
}

