.New .title{
    background:#b3b3b3;
    color:white;
    height:50px;
    line-height:50px;
    padding:0 20px;
}
.New .title img{
    width:20px;
    vertical-align: middle;
    margin:-3px 15px 0 0;
}
.New .aname{
    font-size:14px;
    padding:12px 20px;
    border-bottom:1px solid #f1f0f0;
}
.New .aname img{
    width:30px;
    vertical-align: middle;
    padding-right:5px;
    margin-left:-5px;
}
.New .aname input{
    width:90%;
    border:none;
    height:25px;
    outline:none;
}
.New .time,.repreat{
    font-size:14px;
    padding:16px 20px;
    border-bottom:1px solid #f1f0f0;
    color:#676767;
}
.New .time img{
    width:21px;
    vertical-align: middle;
    padding-right:10px;
    margin-top:-3px;
}
.New .time input{
    border:none;
    margin-left:3px;
    outline:none;
}
.New .repreat img{
    width:21px;
    height:21px;
    vertical-align: middle;
    padding-right:10px;
    margin-top:12px;
}
.New .repreat label{
    border:none;
    background:none;
    color:#676767;
    padding:0 5px;
    height:22px ;
    font-size:12px;
}
.New .repreat input[type='radio']{
    vertical-align: middle;
}
.New  .save{
    margin:20px 0 0 20px;
}
.New  .save button{
    background:#4c9e9e;
    border:none;
    color:white;
    padding:8px 15px;
    border-radius:3px;
}


.New input::-webkit-input-placeholder{
    color:#676767;
}
.New input::-moz-placeholder{   /* Mozilla Firefox 19+ */
    color:#676767;
}
.New input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
    color:#676767;
}
.New input:-ms-input-placeholder{  /* Internet Explorer 10-11 */ 
    color:#676767;
}


/* 设置重复页面 */
.New .set-repreat{
    position:fixed;
    z-index:20;
    width:100%;
    height:100%;
    top:0;
    right:0;
    background:rgba(0,0,0,0.3);
}
.New .set-repreat .set-repreat-back{
    width:280px;
    height:230px;
    background:white;
    margin:80px auto;
    border-radius:5px;
}