.Person{
    padding:30px 30px 80px;
}
.Person .header-img {
    width:100%;
    text-align:center;
}
.Person .header-img img{
    width:90px;
    border:3px dashed #676767;
    border-radius:100%;
}
.Person .name{
    width:100%;
    text-align:center;
    margin-top:15px;
    color:#676767;
}
.Person .Person-message{
    margin-top:20px;
    /* border:1px solid #676767; */
    overflow:hidden;
    padding:10px 0;
}
.Person .Person-message .tel{
    color:#676767;
    float:left;
}
.Person .Person-message .tel img{
    width:25px;
    vertical-align: middle;
    padding-right:8px;
}
.Person .Person-message .num{
    text-align:right;
    color:#b3b3b3;
}
.Person .Person-message .num img{
    width:16px;
}
.Person .Person-message .num .num-font{
    color:#4c9e9e;
}
.Person .Person-message .tel .exit{
    color:red;
}